.main-header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 84.9%;
  max-width: 1160px;
  min-height: 38px;
  margin: 0 auto;
  padding-top: 35px;
}
@media (max-width: 1023px) {
  .main-header__wrapper {
    min-width: 678px;
    width: 88.3%;
