  }
}
@media (max-width: 767px) {
  .main-header__wrapper {
    position: relative;
    min-width: 290px;
    width: 90.62%;
    padding-top: 20px;
  }
  .main-header__wrapper[data-header-state=no-js] {
    flex-direction: column;
    justify-content: flex-start;
    transition: 0.5s;
  }
  .main-header__wrapper[data-header-state=open] {
    position: absolute;
    top: 0;
    right: 0;
    flex-direction: column;
    justify-content: flex-start;
    width: 89%;
    min-width: 285px;
    min-height: 100vh;
    margin-right: 0;
    padding: 20px 4.7% 0 15px;
    background-color: #f9fbfd;
    transition: 0.5s;
  }
}

.main-header__link {
  margin-bottom: auto;
  width: 70px;
  height: 38px;
  cursor: pointer;
}
.main-header__link svg {
  width: 100%;
  height: 100%;
  fill: #f9fbfd;
}
@media (max-width: 1023px) {
  .main-header__link {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .main-header__link {
    position: relative;
    z-index: 5;
    width: 42px;
    height: 22px;
    margin-right: auto;
  }
  .main-header__link[data-header-state=no-js] svg {
    fill: #011c40;
  }
  .main-header__link[data-header-state=open] {
    margin-bottom: 0;
  }
  .main-header__link[data-header-state=open] svg {
    fill: #011c40;
  }
}
@media (hover: hover) {
  .main-header__link:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}
.main-header__link.focus-visible:focus {
  opacity: 0.6;
}

.main-header__toggler {
  display: none;
}
@media (hover: hover) {
  .main-header__toggler:hover:not(.focus-visible) {
    opacity: 0.6;
  }
}
.main-header__toggler.focus-visible:focus {
  opacity: 0.6;
}
@media (max-width: 767px) {
  .main-header__toggler {
    display: block;
    width: 24px;
    height: 14px;
    padding: 0;
    background-color: inherit;
    border: none;
    background-position: center;
    background-repeat: no-repeat;
    cursor: pointer;
  }
  .main-header__toggler[data-header-state=closed] {
    background-image: url("../img/svg/burger.svg");
  }
  .main-header__toggler[data-header-state=open] {
    position: absolute;
    top: 25px;
    right: 4.7%;
    background-image: url("../img/svg/cross.svg");
  }
  .main-header__toggler[data-header-state=no-js] {
    display: none;
  }
