    margin-bottom: 40px;
    font-size: 16px;
    line-height: 22px;
  }
}

.form-section__info {
  width: clamp(100px, 48.1%, 500px);
}
@media (max-width: 1023px) {
  .form-section__info {
    width: 100%;
  }
}

.form {
  width: 403px;
  padding: 50px;
  background-color: #011c40;
  box-shadow: 0 4px 45px rgba(1, 28, 64, 0.15);
}
@media (max-width: 1023px) {
  .form {
    width: 96.1%;
  }
}
@media (max-width: 767px) {
  .form {
    width: 100%;
    padding: 40px 30px;
  }
}

.form__text {
  width: 100%;
  margin: 0;
  margin-bottom: 35px;
  font-size: 18px;
  line-height: 27px;
  font-weight: 500;
  text-align: start;
  color: #f9fbfd;
}
@media (max-width: 767px) {
  .form__text {
    display: block;
    width: 93%;
    margin: 0 auto 35px auto;
    line-height: 25px;
    text-align: center;
  }
}

.form__field {
  display: block;
  margin-bottom: 20px;
}
.form__field input {
  width: 100%;
  height: 100%;
  padding: 12.5px 16px;
  font-size: 14px;
  line-height: 21px;
  color: #011c40;
  background: #f9fbfd;
  border: none;
}
.form__field input::placeholder {
  font-size: 14px;
  line-height: 21px;
  color: rgba(1, 28, 64, 0.7);
}
@media (hover: hover) {
  .form__field input:hover:not(.focus-visible) {
    outline: 3px solid #badefb;
  }
}
.form__field input.focus-visible:focus {
  outline: 3px solid #badefb;
}
.form__field input:focus:required:invalid {
  outline: 3px solid #badefb;
}

.form__submit-button {
  width: 100%;
  min-height: 43px;
  margin-top: 20px;
  padding: 10px;
  font-size: 14px;
  line-height: 21px;
  font-weight: 600;
  color: #011c40;
  background-color: #f9fbfd;
  border: none;
  cursor: pointer;
}
.form__submit-button:hover {
  opacity: 0.6;
}

.custom-checkbox {
  margin-top: 5px;
  margin-bottom: 10px;
}
@media (max-width: 767px) {
  .custom-checkbox {
    width: 78.3%;
    margin-top: 0;
  }
}
.custom-checkbox label {
  display: flex;
  cursor: pointer;
}
@media (hover: hover) {
  .custom-checkbox label:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.custom-checkbox input[type=checkbox]:checked + .custom-checkbox__mark::before {
  display: block;
}
.custom-checkbox:focus-within .custom-checkbox__mark {
  outline: 3px solid #badefb;
}

.custom-checkbox__label {
  display: block;
  margin: 0;
  font-size: 10px;
  line-height: 13px;
  font-weight: 400;
  color: #f9fbfd;
}
@media (max-width: 767px) {
  .custom-checkbox__label {
    line-height: 12px;
  }
}

.custom-checkbox__mark {
  position: relative;
  top: -2.5px;
  display: block;
  min-width: 18px;
  height: 18px;
  margin-right: 5px;
  background-color: #f9fbfd;
  border: none;
}
@media (max-width: 767px) {
  .custom-checkbox__mark {
    top: 0;
    min-width: 20px;
    height: 20px;
    margin-right: 8px;
  }
}
.custom-checkbox__mark::before {
  position: absolute;
  content: "";
  display: none;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-image: url("../img/svg/done.svg");
  background-position: top 6px left 4px;
  background-repeat: no-repeat;
}

